@import url(https://fonts.googleapis.com/css?family=Poppins:300,400&display=swap);
body {
    margin: 0;
    padding: 0;
    border: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #000;
    color: #efefef;
}

* {
    margin: 0;
    padding: 0;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.App {
    max-width: 1400px;
    margin: auto;
}
#Navbar {
    height: 70px;
    position: relative;
}

#logo,
#link-behind,
#navbar-links {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

body {
    font-family: 'Poppins', sans-serif;
}

#logo {
    position: absolute;
    top: 20px;
    left: 5%;
    line-height: 20px;
    font-size: 20px;
    z-index: 3;
}

#toggler {
    position: absolute;
    top: 25px;
    right: 5%;
    width: 30px;
    height: 30px;
    z-index: 3;
}

#toggler div {
    height: 2px;
    position: absolute;
    background-color: #efefef;
    -webkit-transition: -webkit-transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1);
    transition: -webkit-transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1);
    transition: transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1);
    transition: transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1), -webkit-transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

#toggler div:nth-child(1) {
    width: 30px;
    right: 0;
}

#toggler div:nth-child(2) {
    width: 20px;
    top: 7px;
    right: 0;
}

#toggler.activated div:nth-child(1) {
    -webkit-transform: translate(4px, 5px) rotate(35deg);
            transform: translate(4px, 5px) rotate(35deg);
}

#toggler.activated div:nth-child(2) {
    -webkit-transform: translate(3px, -1px) rotate(-36deg);
            transform: translate(3px, -1px) rotate(-36deg);
    width: 30px
}

#navbar-links {
    width: 100%;
    height: 140vh;
    position: absolute;
    top: -20vh;
    left: 0;
    margin-top: 40vh;
    list-style: none;
    text-align: center;
    -webkit-filter: opacity(0);
            filter: opacity(0);
    pointer-events: none;
    -webkit-transition: opacity 400ms ease-in-out;
    transition: opacity 400ms ease-in-out;
    z-index: 2;
}

#navbar-links li {
    margin: 5px 0;
    padding: 16px 0;
    cursor: pointer;
}

#blured {
    width: 100%;
    height: 140vh;
    position: absolute;
    left: 0;
    top: -20vh;
    background-color: rgba(0, 0, 0, 0);
    z-index: 1;
    -webkit-transition: background-color 200ms ease-in-out;
    transition: background-color 200ms ease-in-out;
}

#blured.activated {
    background-color: rgba(0, 0, 0, 0.95);
}

#navbar-links.activated {
    -webkit-filter: opacity(1);
            filter: opacity(1);
    pointer-events: all;
}

#link-behind {
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translate(-50%, -18%);
            transform: translate(-50%, -18%);
    font-size: 1200%;
    max-width: 100vw;
    z-index: -1;
    color: rgba(200, 200, 200, 0.16);
}
